<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div class="task pageSpace h100">
        <div>
            <a-form :label-width="80">
                <a-row :gutter="[24,24]" style="margin:0">
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white" class="searchCss">分析对象</label>
                        <a-select
                                class="searchContentCss"
                                v-model="objValue.value"
                                filterable
                                remote
                                :max-tag-count="1"
                                @change="objChange"
                        >
                            <a-select-option v-for="item in match.objName" :key="item" :value="item">{{item}}
                            </a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">开始时间</label>
                        <a-date-picker
                                class="searchContentCss"
                                format="YYYY-MM-DD HH:mm:ss"
                                :show-time="{defaultValue:moment('00:00:00','HH:mm:ss')}"
                                @change="timePickStart"
                        ></a-date-picker>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">结束时间</label>
                        <a-date-picker
                                class="searchContentCss"
                                format="YYYY-MM-DD HH:mm:ss"
                                :show-time="{defaultValue:moment('00:00:00','HH:mm:ss')}"
                                @change="timePickEnd"
                        ></a-date-picker>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">协议</label>
                        <a-select class="searchContentCss" v-model="agrementValue.value" mode="multiple">
                            <a-select-option :value="item.code" v-for="item of ruleGroupData" :key="item.id">
                                {{item.name}}
                            </a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">归属地</label>
                        <a-select
                                class="searchContentCss"
                                v-model="locationValue.value"
                                filterable
                                remote
                                :max-tag-count="1"
                        >
                            <a-select-option value="all">全部</a-select-option>
                            <a-select-option value="inchina">中国</a-select-option>
                            <a-select-option value="nochina">境外</a-select-option>
                            <a-select-option value="norn">局域网</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">单个邮箱成功>=</label>
                        <a-input
                                class="searchContentCss"
                                addon-after="次"
                                v-model="successSize.value"
                        ></a-input>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">单个邮箱失败>=</label>
                        <a-input
                                class="searchContentCss"
                                addon-after="次"
                                v-model="failSize.value"
                        ></a-input>
                    </a-col>
                </a-row>
                <a-row>
                    <a-col :lg="2" style="float: right">
                        <a-button type="primary" @click="exportCsv()" :loading="exportLoading">
                            <a-icon type="export"></a-icon>
                            导出
                        </a-button>
                    </a-col>
                    <a-col :lg="2" style="float: right">
                        <a-button type="primary" @click="search()">
                            <a-icon type="search"></a-icon>
                            查询
                        </a-button>
                    </a-col>
                </a-row>
            </a-form>
        </div>
        <div style="margin-top: 40px">
            <a-table
                    :columns="columns"
                    rowKey="id"
                    :pagination="{showSizeChanger:true,showQuickJumper:true,pageSizeOptions:sizeList}"
                    :dataSource="searchData"
                    :loading="loading"
                    size="default"
            >
        <span slot="req_ip" slot-scope="text,record">
                    <span>{{record.req_ip}}</span>
                </span>
            </a-table>
            <p style="float: right;margin-left: 64px;margin-top: 16px; font-size: 15px;">总条数：{{total}}</p>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import moment from "moment";
    import {csvHelper} from "../../../../../common/csv-helper";

    export default {
        name: "blastingAnalyze",
        data() {
            return {
                total: 0, // 总数
                // 定义开始时间
                starttimes: "",
                // 定义结束时间
                endtimes: "",
                loading: false,
                exportLoading: false,
                current: 1,
                pageSize: 10,
                sizeList: ["5", "10", "20", "30"],
                fistUnit: '',
                columns: [
                    {
                        dataIndex: "req_ip",
                        title: "IP",
                        scopedSlots: {customRender: 'req_ip'},
                        className: '${color:\'#000000\'}'
                    },
                    {
                        dataIndex: "country",
                        title: "归属地",
                    },
                    {
                        dataIndex: "successCount",
                        title: "成功次数",
                        sorter: (a, b) => a.successCount - b.successCount
                    },
                    {
                        dataIndex: "failCount",
                        title: "失败次数",
                        sorter: (a, b) => a.failCount - b.failCount
                    },
                ],
                // 协议类型
                agrementValue: {
                    label: "rule_groups",
                    value: [],
                },
                // 归属地
                locationValue: {
                    label: "countryTag",
                    value: "all",
                },
                // 单位
                unitValue: {
                    label: "unit",
                    value: [],
                },
                // 分析对象
                objValue: {
                    label: "ana_obj",
                    value: "",
                },
                // 登录成功次数
                successSize: {
                    lable: "successThreshold",
                    value: "1",
                },
                // 登录失败次数
                failSize: {
                    lable: "failThreshold",
                    value: "1",
                },
                // 分页
                pageParam: {
                    page: 1,
                    size: 10,
                },
                // 表格数据
                searchData: [],
                // 表格内容
                data1: [],
                // 筛选字段
                match: {
                    countries: [],
                    objName: [],
                },
                units: [],
                // 单位id
                unitNmae: "",
                firstUnitName: "",
                csvHelper,
                ruleGroupData: [],
            };
        },
        computed: {
            ...mapState(["selectUnitEn", "objByCurrentUnit"]),
        },
        methods: {
            moment,
            onChange(date, dateString) {
                console.log(date, dateString);
            },
            // 选择开始时间控件
            timePickStart(time) {
                this.starttimes = new moment(time).format("YYYY-MM-DD HH:mm:ss");
            },
            // 选择结束时间控件
            timePickEnd(time) {
                this.endtimes = new moment(time).format("YYYY-MM-DD HH:mm:ss");
            },
            // 获取表格数据
            getTableData() {
                this.loading = true;
                this.pageParam.page = 1;
                let params = Object.assign(
                    {},
                    {
                        beginTime: this.starttimes,
                        endTime: this.endtimes,
                        units: [sessionStorage.getItem('selectUnitEn')],
                        country: this.locationValue.value,
                        successThreshold: this.successSize.value,
                        failThreshold: this.failSize.value,
                        ana_obj: this.objValue.value,
                        rule_groups: this.agrementValue.value,
                        top: 10000,
                    }
                );
                this.$API.getForceLoginAgg(params).then((res) => {
                    if (res.code === 1000) {
                        this.searchData = res.data;
                        this.total = res.data.length;
                        this.loading = false;
                    } else {
                        this.searchData = [];
                        this.loading = false;
                    }
                });
            },
            // 获取分析对象
            getAnalysisObj() {
                let params = {name: this.selectUnitEn};
                this.$API.getAnalysisObj(params).then((res) => {
                    if (res.code === 1000) {
                        this.match.objName = res.data;
                        this.objValue.value = res.data[0];
                        this.selectByObjectName(this.objValue.value);
                    } else {
                        this.match.countries = "";
                    }
                });
            },
            // 分析对象变化事件
            objChange() {
                this.selectByObjectName(this.objValue.value);
            },
            // 查询协议类型
            getAgreement(objId) {
                this.$API.searchRuleGroup({objectId: objId}).then((res) => {
                    if (res.code === 1000) {
                        this.ruleGroupData = res.data;
                    } else {
                        this.ruleGroupData = [];
                    }
                });
            },
            // 根据分析对象获取分析对象id
            selectByObjectName(objName) {
                let params = {
                    name: objName
                };
                this.$API.selectByObjectName(params).then((res) => {
                    if (res.code === 1000) {
                        this.objId = res.data.id;
                        this.getAgreement(this.objId);
                    } else {
                        this.objId = "";
                    }
                });
            },
            // 查询按钮
            search() {
                this.page = 1;
                this.getTableData();
            },
            // 导出csv数据
            exportCsv() {
                this.exportLoading = true;
                if (this.searchData.length) {
                    // 配置表头
                    const field = this.columns
                        .map((item) => ({label: item.title, value: item.dataIndex}))
                        .concat([]);
                    this.csvHelper.exports(
                        `${this.agrementValue.value}`,
                        this.searchData,
                        field
                    );
                    this.exportLoading = false;
                } else {
                    this.$message.warning("当前没有数据可以导出");
                    this.exportLoading = false;
                }
            },
            // 跳转到详情
            ipClick(val) {
                console.log(val);
                let beginDate = "";
                let endDate = "";
                if (this.starttimes) {
                    beginDate = this.starttimes;
                }
                if (this.endtimes) {
                    endDate = this.endtimes;
                }
                let rule_group_str = "";
                for (let i = 0; i < this.agrementValue.value.length; i++) {
                    rule_group_str += `rizhi=${this.agrementValue.value[i]}&`
                }
                var url = "/#/business/search/searchLog";
                var params = `?&beginDate=${beginDate}&endDate=${endDate}&req_ip=${val.req_ip}&${rule_group_str}unitList=${this.unitValue.value}`;
                window.open(url + params, "_blank");
            },
        },
        created() {
            this.getAnalysisObj();
        },
        watch: {
            // 监听单位变化 提交vuex action接口 获取分析对象数据
            selectUnitEn: {
                handler: function () {
                    this.$store.dispatch('getObjByUnit', this);
                    this.getAnalysisObj();
                }
            },
        },
    };
</script>

<style scoped>

